@import url('https://fonts.googleapis.com/css2?family=Gruppo&family=Tourney&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-size: 2.4rem;
    color: #5a3e2b;
    font-family: "Quicksand", sans-serif;
    font-weight: 500;
}

.app {
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-rows: auto auto 1fr auto;
}

header {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #FBCD59;
}

header img {
    display: inline-block;
    width: 90px;
    height: 90px;
    margin: 0 1.8rem;
}

h1 {
    display: inline-block;
    text-align: center;
    font-family: "Tourney", sans-serif;
    font-size: 7rem;
    text-transform: uppercase;
    font-weight: 400;
    word-spacing: 30px;
    padding: 1.8rem 0;
}

.add-form {
    background-color: #EF9922;
    padding: 1.8rem 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
}

h3 {
    margin-right: 1.6rem;
    font-size: 2.4rem;
}

button,
select,
input {
    background-color: #ffebb3;
    color: #5a3e2b;
    font-family: inherit;
    border: none;
    border-radius: 10rem;
    padding: 1.2rem 3.2rem;
    font-weight: 700;
    font-size: 1.8rem;
    cursor: pointer;
}

input[type='text'] {
    cursor: text;
}

button:hover {
    background-color: #ffe7a3;
}

button:focus, select:focus, input:focus {
    outline-color: #5a3e2b;
}

.add-form button {
    text-transform: uppercase;
    background-color: #C7EDE6;
    transition: all .3s;
}

.add-form button:hover {
    background-color: #bee6dc;
}

.actions button,
.list select {
    text-transform: uppercase;
    padding: 0.8rem 2.4rem;
    font-size: 1.6rem;
    font-weight: 700;
    margin: 0 0.8rem;
}

.list ul {
    list-style: none;
    width: 80%;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.2rem;
    justify-content: center;
    align-content: start;
}

.list li {
    display: flex;
    align-items: center;
    gap: 1.2rem;
}

li > span {
    word-break: break-all;
    max-width: 20rem;
}

input[type="checkbox"] {
    height: 2rem;
    width: 2rem;
    accent-color: #EF9922;
}

.list {
    background-color: #5a3e2b;
    color: #ffebb3;
    padding: 4rem 0;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3.2rem;
    align-items: center;
}

.list li button {
    cursor: pointer;
    background: none;
    color: #ff6969;
    border: none;
    /*font-size: 1.8rem;*/
    font-size: 3.2rem;
    padding: 0;
    transform: translateY(2px);
}

.stats {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #C7EDE6;
    font-weight: 700;
    padding: 0.2rem 0;
}

.stats img {
    width: 50px;
    margin: 0.5rem;
}

@media only screen and (max-width: 61.25em) {
    html {
        font-size: 50%;
    }

    h1 {
        font-size: 6rem;
    }

    h3 {
        width: 100%;
        text-align: center;
        padding-bottom: 0.8rem;
    }

    .list ul {
        grid-template-columns: auto;
    }

    button, select, input {
        padding: 1.2rem 1.8rem;
    }

    .actions button, .list select {
        padding: 0.8rem 1.8rem;
    }

    .stats {
        padding-right: 1rem;
    }
}